.GameUi {
  display: grid;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  background-attachment: fixed;
  width: 100%;
  overflow: hidden;
}

.fullScreen {
  width: 100%;
  height: 100%;
}

.button-full {
  text-align: center;
  margin-bottom: 10px;
}
.button-full input {
  padding: 15px;
  font-size: 13px;
  border-radius: 10px;
  cursor: pointer;
  background-color: var(--primary-color);
}
.button-full input:hover {
}
.use-desktop {
  visibility: hidden;
}

@media screen and (max-width: 800px) {
  .GameUi {
    visibility: hidden;
  }
  .button-full {
    visibility: hidden;
  }
  .use-desktop {
    color: var(--primary-color);
    visibility: visible;
  }
}
