.about
{
    margin-top: calc(50px + 10vw);
}

.description
{   
    margin-bottom: 100px; 
}

.description-details
{  
    width: 35%;
}

.description-map
{
    justify-content: flex-end !important;
}

p
{     
    margin-top: 15px; 
    font-family: 'Poppins'; 
    font-weight: 200;  
}

.last-item {
    order: 1; 
}
.img-teamwork,.img-build,.img-vision, .description-map{
    display: flex;
    justify-content: center;
    width: 40%;   
}

.img-teamwork img,.img-vision img,.img-build img, .description-map iframe
{
    margin-bottom: 40px;
}
.img-teamwork img{
    width: 120%; 
    margin: auto;
} 
.img-vision img{
    width: 60%;  
} 
.img-build img{
    width: 70%; 
    margin: auto;
}
.description-map #iframeGoogleMap{
    width: 35vw;
    height: 30vw;
    margin: auto 0;
    border-radius: calc(5px + .6vw);
    border: 0;
}

@media screen and (max-width: 800px)
{
    .description 
    { 
        margin-top: 40px; 
    }
    .description-details {
        width: 80%;   
    }
  
    p {
        text-align: center; 
        letter-spacing: 1px;
    }

    .img-teamwork,.img-build,.img-vision, .description-map{
        width: 80%; 
        margin: auto; 
    }
   
    .img-teamwork img{
        width: 120%;  
        margin-bottom: 15px; 
    } 
    .img-vision img{
        width: 60%;  
        margin: auto; 
        margin-bottom: 30px; 
    } 
    .img-build img{
        width: 80%; 
        margin-bottom: 15px;   
    } 
    .description-map #iframeGoogleMap{
        width: 79vw;
        height: 50vw;
        margin-bottom: 40px;
    }
}